import { FC } from "react";
import Advertisement from "../../advertisement";
import { useMediaQuery } from "react-responsive";
import FeedCard from "../../feedCard";
import { TSplitSoloNewsBySections } from "./types";

type TProps = TSplitSoloNewsBySections;

export const Section: FC<TProps> = ({
  firstSectionItems,
  secondSectionItems,
  thirdSectionItems,
}) => {
  const isNotDesktop = useMediaQuery({ minWidth: "320px", maxWidth: "1439px" });

  return (
    <>
      <section className={"sectionPage__mainContent mainContent"}>
        {isNotDesktop ? (
          <section className={"mainContent__mainCards"}>
            <Advertisement />
            <Advertisement />
            <Advertisement />
            {firstSectionItems.map((news, index) => (
              <FeedCard type={index === 0 ? "full" : "half"} {...news} />
            ))}
          </section>
        ) : (
          <>
            <section className={"mainContent__asideCardsList asideCardsList"}>
              {firstSectionItems.map((news, index) => (
                <FeedCard type={index === 0 ? "full" : "half"} {...news} />
              ))}
            </section>
            <aside className={"mainContent__asideAds"}>
              <Advertisement />
              <Advertisement />
              <Advertisement />
            </aside>
          </>
        )}
      </section>
      {secondSectionItems.length ? (
        <section className={"sectionPage__additionalContent additionalContent"}>
          <Advertisement />
          <Advertisement />
          <Advertisement />
          <Advertisement />
          <Advertisement />
          <Advertisement />
          <Advertisement />
          {secondSectionItems.map((news) => (
            <FeedCard type={"half"} {...news} />
          ))}
        </section>
      ) : null}
      {/*<section className={"sectionPage__recommendedContent recommendedContent"}>*/}
      {/*  <Recommended heading={""} />*/}
      {/*</section>*/}
      {thirdSectionItems.length ? (
        <section className={"sectionPage__subContent subContent"}>
          <Advertisement />
          <Advertisement />
          <Advertisement />
          {thirdSectionItems.map((news) => (
            <FeedCard type={"half"} {...news} />
          ))}
        </section>
      ) : null}
    </>
  );
};
