import {
  TGetMainNews,
  TNewsByAlias,
  TNewsById,
  TNewsCategories,
  TNewsLine,
} from "../types/api";

const API_PATH = "https://apiv2.v102.ru";

export const getMainNews = async (): Promise<TGetMainNews | undefined> => {
  try {
    return await fetch(`${API_PATH}/news/main`).then((res) => {
      if (!res.ok) {
        throw new Error(res.statusText);
      }

      return res.json();
    });
  } catch (e) {
    console.error(e);
  }
};

export const getNewsById = async (
  id: number,
  options: {
    doesNeedActualId?: boolean;
  } = {}
): Promise<TNewsById | undefined> => {
  const { doesNeedActualId } = options;
  try {
    return await fetch(
      `${API_PATH}/news?newsId=${id}${
        doesNeedActualId ? `&doesNeedActualId=${doesNeedActualId}` : ""
      }`
    ).then((res) => {
      if (!res.ok) {
        throw new Error(res.statusText);
      }

      return res.json();
    });
  } catch (e) {
    console.error(e);
  }
};

