export const getNewsByAlias = async (
  id: number,
  skip?: number,
  take = 37,
  ignoredId?: string
): Promise<TNewsByAlias | undefined> => {
  try {
    return await fetch(
      `${API_PATH}/news?categoryId=${id}${skip ? `&skip=${skip}` : ""}${
        take ? `&take=${take}` : ""
      }${ignoredId ? `&ignoredId=${ignoredId}` : ""}`
    ).then((res) => {
      if (!res.ok) {
        throw new Error(res.statusText);
      }

      return res.json();
    });
  } catch (e) {
    console.error(e);
  }
};

